import { PropsWithChildren } from "react"
import { generatePath, useNavigate } from "react-router-dom"
import { WATCHLIST_FEATURE_NAME } from "@rp/common/settings"
import { useIsWatchlistLimitMet } from "@rp/react/common/hooks"
import { useAccount } from "@rp/react/common/providers"
import { BOTTOM_NAV_HEIGHT } from "@rp/react/web/common/composite-components"
import { FabExpanding } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { useModal } from "@rp/react/web/common/providers"
import { Add } from "@rp/react/web/mui/icons"
import { WATCHLIST_EDIT } from "@rp/react/web/router"

const NewWatchlistButtonContainer = ({ children }: PropsWithChildren) => {
  const { isMobile } = useDeviceInfo()
  const navigate = useNavigate()

  const { quota } = useAccount()
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()
  const hasMetWatchlistLimit = useIsWatchlistLimitMet(quota)

  const {
    watchlistModal: { handleOpen: handleOpenNew },
  } = useModal()
  return (
    <>
      {children}
      {isMobile ? (
        <FabExpanding
          animateTo={180}
          Icon={<Add />}
          onClick={() =>
            hasMetWatchlistLimit
              ? planComparisonHandleOpen(WATCHLIST_FEATURE_NAME)
              : handleOpenNew(undefined, watchlist => {
                  navigate(generatePath(WATCHLIST_EDIT, { id: watchlist.id }))
                })
          }
          yOffset={BOTTOM_NAV_HEIGHT}
        >
          New Watchlist
        </FabExpanding>
      ) : null}
    </>
  )
}

export { NewWatchlistButtonContainer }
